.wrapper {
    display: flex;
    flex-direction: column;
    margin-top: 5em;
    margin-bottom: 5em;

    header {
        margin-bottom: 2em;
    }

    .faqs {
        width: 60%;

        article {
            padding-right: 2.2em;
            border-radius: 10px;
            box-shadow: 0px 1px 5px rgba(1, 1, 1, 0.2);
            cursor: pointer;
            padding-left: 1em;
            display: flex;
            flex-direction: column;
            justify-content: center;
            margin-bottom: 0.5em;
            width: 100%;
            padding-block: 1em;
            background-color: white;

            .trigger {
                width: 100%;
                padding-block: 0.5em;
                padding-bottom: 0;

                position: relative;

                span.rotated {
                    transform: rotate(90deg);
                }

                span {
                    right: 0px;
                    position: absolute;
                    display: inline-block;
                    transition: transform 1s;

                    svg {
                        color: #9e9e9e;
                    }
                }
            }

            .collapsed {
                padding-top: 0.5em;
                height: 0;
                overflow-y: hidden;
                transition: all 0.5s;
                max-height: 0px;
                color: #6b6b6b;
                line-height: 22px;
            }

            .collapsed.open {
                max-height: 200px;
                height: 100%;
            }
        }
    }
}

@media screen and (max-width: 900px) {
    .wrapper {
        .faqs {
            width: 100%;
        }
    }
}
