.wrapper {
    margin-top: -32px;
    header {
        margin-block: 2em;
    }

    .hero {
        line-height: 30px;
        width: 100%;
        margin-bottom: 10em;
        background-color: #f8f7ff;
        padding-block: 1.5em;

        p {
            line-height: 40px;
            margin-bottom: 2em;
        }
    }
    .teamSection {
        article {
            border-radius: 20px;
            box-shadow: 1px 1px 10px rgba(1, 1, 1, 0.2);
            img {
                border-radius: 20px;
                border-bottom-left-radius: 0;
                border-bottom-right-radius: 0;
                width: 100%;
                height: 320px;
                object-fit: cover;
            }

            .bottom {
                padding-inline: 1em;
                padding-block: 1em;

                .designation {
                    color: rgba(1, 1, 1, 0.8);
                }
            }
        }
    }
}
